"use client";

import Script from 'next/script';

const TikTokPixel = () => {
  // Using a real-looking test pixel ID since we don't have a real one
  const pixelId = process.env.NEXT_PUBLIC_TIKTOK_PIXEL_ID || "CGXXXXXXXXXX";
  const contentId = process.env.NEXT_PUBLIC_TIKTOK_CONTENT_ID || pixelId;
  
  return (
    <>
      <Script id="tiktok-pixel-script" strategy="afterInteractive">
        {`
          !function (w, d, t) {
            w.TiktokAnalyticsObject = t;
            var ttq = w[t] = w[t] || [];
            ttq.methods = ["page","track","identify","instances","debug","on","off","once","ready","alias","group","enableCookie"];
            ttq.setAndDefer = function(t, e) {
              t[e] = function() {
                t.push([e].concat(Array.prototype.slice.call(arguments,0)))
              }
            };
            for (var i = 0; i < ttq.methods.length; i++) {
              ttq.setAndDefer(ttq, ttq.methods[i]);
            }
            ttq.instance = function(t) {
              var e = ttq._i[t] || [];
              e.push(["page"]);
              return e
            };
            ttq.load = function(e, n) {
              var i = "https://analytics.tiktok.com/i18n/pixel/events.js";
              ttq._i = ttq._i || {};
              ttq._i[e] = [];
              ttq._i[e].push(n);
              var o = document.createElement("script");
              o.type = "text/javascript", o.async = !0, o.src = i + "?sdkid=" + e + "&lib=" + t;
              var a = document.getElementsByTagName("script")[0];
              a.parentNode.insertBefore(o, a)
            };

            // Initialize TikTok Pixel with both pixel ID and content ID
            ttq.load("${pixelId}", {
              content_id: "${contentId}",
              pixel_id: "${pixelId}"
            });
          }(window, document, 'ttq');
        `}
      </Script>
    </>
  );
};

export default TikTokPixel;
